.button {
	font-size: 16px;
	font-weight: 600;
	color: white;

	max-width: 200px;
	width: 100%;

	margin-top: 15px;

	background-color: #004795;
	padding: 14px 20px;
	border-radius: 8px;

	display: flex;
	justify-content: center;
	align-items: center;
	gap: 20px;

	transition: all 0.3s ease 0s;
}

@media (hover: hover) {
	.button:hover {
		background-color: #0270e6;
	}
}

@media (max-width: 500px) {
	.button {
		font-size: 14px;
		padding: 12px 10px;
		max-width: 150px;
	}
	.button img {
		width: 16px;
	}
}
